import { useBoolean } from 'ahooks';
import vaultImage from 'assets/images/vault_team_noletter.jpg';
import Timer from 'components/Timer';
import config from 'config';
import useFetch from 'hooks/useFetch';
import { useEffect } from 'react';
import { assetService } from 'services/blockchain/assetService';
import { store } from 'store';
import { useAppSelector } from 'store';
import { setCoins, setTokenIds } from 'store/actions/globalActions';

import { LoadingButton } from '@mui/lab';
import { Box, Button, Link, Typography } from '@mui/material';
import { useWeb3React } from '@web3-react/core';

function Vault({ sx }) {
  const { active, account } = useWeb3React();
  const { tokenIds, claimable, coins } = useAppSelector(
    (state) => state.global
  );
  const [isClaiming, setIsClaiming] = useBoolean(false);
  // const { data, loading, error } = useFetch(`${config.apiBaseURL}asset`);
  const { data, loading, error } = useFetch(`${config.apiBaseURL}coins`);
  const time = useFetch(`${config.apiBaseURL}time`);

  // useEffect(() => {
  //   setCoins();
  // }, []);

  useEffect(() => {
    if (active) store.dispatch(setTokenIds(account));
  }, [active, account]);

  const onClaim = async () => {
    setIsClaiming.setTrue();
    const tx = await assetService.claimAsset(account, tokenIds);
    if (tx !== null) toast.success('Successfully claimed!');
    setIsClaiming.setFalse();
  };

  return (
    <Box
      sx={{
        backgroundImage: `url(${vaultImage})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center top',
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        padding: '0 0 50px 0',
        '@media (min-width: 425px)': {
          padding: '0 0 50px 0',
        },
        '@media (min-width: 768px)': {
          padding: '0 0 50px 0',
        },
        '@media (min-width: 1024px)': {
          padding: '0 0 100px 0',
        },
        '@media (min-width: 1440px)': {
          padding: '0 0 100px 0',
        },
        '@media (min-width: 1920px)': {
          padding: '0 0 100px 0',
        },
        marginBottom: '15px',
        ...sx,
      }}
    >
      <Box
        sx={{
          padding: '25px 0 40px 0',
          '@media (min-width: 375px)': {
            padding: '25px 0 30px 0',
          },
          '@media (min-width: 425px)': {
            padding: '20px 0',
          },
          '@media (min-width: 768px)': {
            padding: '30px 0',
          },
          '@media (min-width: 1024px)': {
            padding: '40px 0',
          },
          '@media (min-width: 1440px)': {
            padding: '15px 0',
          },
          '@media (min-width: 1920px)': {
            padding: '30px 0',
          },
        }}
      >
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            color: '#db9b13',
            textDecoration: 'none',
            fontSize: 30,
            letterSpacing: 7,
            '@media (min-width: 375px)': {
              fontSize: 40,
              letterSpacing: 10,
            },
            '@media (min-width: 425px)': {
              fontSize: 40,
              letterSpacing: 10,
            },
            '@media (min-width: 768px)': {
              fontSize: 60,
              letterSpacing: 10,
            },
            '@media (min-width: 1024px)': {
              fontSize: 70,
              letterSpacing: 10,
            },
            '@media (min-width: 1440px)': {
              fontSize: 90,
              letterSpacing: 15,
            },
            '@media (min-width: 1920px)': {
              fontSize: 110,
              letterSpacing: 20,
            },
          }}
        >
          THE VAULT
        </Typography>
      </Box>
      {time.data && (
        <Box
          sx={{
            position: 'absolute',
            right: '20px',
            top: '60px',
            '@media (min-width: 768px)': {
              right: '20px',
              top: '96px',
            },
            '@media (min-width: 1024px)': {
              right: '20px',
              top: '112px',
            },
            '@media (min-width: 1440px)': {
              transform: 'rotate(-10deg)',
              right: '20px',
              top: '50px',
            },
          }}
        >
          <Timer targetTime={time.data.lockTime} />
        </Box>
      )}
      {!active && (
        <Box
          sx={{
            display: 'grid',
            rowGap: 2,
            columnGap: 4,
            '@media (min-width: 1025px)': {
              gridTemplateColumns: 'repeat(2, 1fr)',
            },
          }}
        >
          {config['assets'] &&
            config['assets'].map((asset) => (
              <Link
                href={config['coinGeckoBaseUrl'] + asset['url']}
                sx={{
                  fontFamily: 'LapsusBold',
                  display: 'block',
                  color: 'black',
                  textDecoration: 'none',
                  fontSize: 25,
                  '@media (min-width: 768px)': {
                    WebkitTextStroke: '2px #d89810',
                    fontSize: 40,
                  },
                  '@media (min-width: 1024px)': {
                    fontSize: 50,
                  },
                  WebkitTextStroke: '1px #d89810',
                }}
              >
                {`${asset['name']} (${asset['symbol']})`}
              </Link>
            ))}
        </Box>
      )}
      {active && (
        <Box
          sx={{
            marginTop: '20px',
            width: '90%',
            '@media (min-width: 1024px)': {
              marginTop: '50px',
            },
          }}
          flex
          flexBasis="column"
          alignItems="center"
        >
          <Box
            sx={{
              maxWidth: '600px',
              margin: 'auto',
            }}
          >
            {data &&
              data.map((coin) => (
                <Typography
                  sx={{
                    fontFamily: 'LapsusBold',
                    fontSize: 23,
                    '@media (min-width: 425px)': {
                      fontSize: 25,
                    },
                    '@media (min-width: 768px)': {
                      WebkitTextStroke: '2px #d89810',
                      fontSize: 40,
                    },
                    '@media (min-width: 1024px)': {
                      fontSize: 50,
                    },
                    textAlign: 'left',
                    WebkitTextStroke: '1px #d89810',
                  }}
                >
                  {`${coin.name}: ${Number(
                    (tokenIds.length * coin.amount) / 1200
                  ).toFixed(4)} ${coin.symbol}`}
                </Typography>
              ))}
          </Box>
          <Box textAlign="center">
            <Button
              sx={{
                marginTop: '30px',
                width: '70%',
                fontFamily: 'LapsusBold',
                fontSize: '20px',
                padding: '15px 10px',
                '@media (min-width: 768px)': {
                  marginTop: '70px',
                  fontSize: 40,
                },
                '@media (min-width: 1024px)': {
                  fontSize: 50,
                },
                color: '#fac718',
                backgroundColor: 'black',
                border: '10px solid white',
                borderRadius: '60px',
                maxWidth: '900px',
                lineHeight: '1',
                letterSpacing: '7px',
                transform: 'rotate(-5deg)',
                opacity: claimable ? 1 : 0.5,
                '&:disabled': {
                  color: '#fac718',
                },
                '&:hover': {
                  backgroundColor: 'rgba(0, 0, 0, 0.7)',
                },
              }}
              onClick={onClaim}
              disabled={!claimable}
            >
              CLAIM YOUR ASSETS NOW
            </Button>
          </Box>
        </Box>
      )}
      {/* {tokenIds}
      <LoadingButton
        disabled={!(active && claimable && tokenIds.length > 0)}
        onClick={onClaim}
        loading={isClaiming}
      >
        Claim
      </LoadingButton> */}
    </Box>
  );
}

export default Vault;
