import body_tablet from 'assets/images/body_tablet.jpg';
import sidebarURL from 'assets/images/sidebar.jpg';
import Header from 'components/Header';
import WelcomeComponent from 'components/WelcomeComponent';
import React from 'react';

import { Box, Container } from '@mui/material';

function MainContainer({ children }) {
  return (
    <Box>
      <Header />
      <Container
        disableGutters
        sx={{
          display: 'flex',
          padding: '0 15px',
          maxWidth: {
            lg: '2030px',
          },
          width: '100%',
          backgroundPosition: 'center top',
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundImage: `url(${body_tablet})`,
        }}
      >
        <Box
          sx={{
            display: 'none',
            width: '150px',
            backgroundImage: `url(${sidebarURL})`,
            backgroundSize: 'contain',
            '@media (min-width: 769px)': {
              display: 'block',
            },
          }}
        />
        <Box
          sx={{
            width: '100%',
            display: 'flex',
            justifyContent: 'flex-start',
            '@media (min-width: 769px)': {
              width: 'calc(100% - 165px)',
            },
          }}
        >
          {children}
        </Box>
        {/* <WelcomeComponent /> */}
        {/* {isLoading && <WelcomeComponent />} */}
        {/* <BackgroundImage setLoading={setIsLoading} /> */}
      </Container>
    </Box>
  );
}

export default MainContainer;
