import mintImg from 'assets/images/mint.gif';
import mintBox from 'assets/images/mintbox_noletter.jpg';
import config from 'config';
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Carousel from 'react-multi-carousel';
import { toast } from 'react-toastify';
import { sugarHeadService } from 'services/blockchain/sugarHeadService';
import { useAppSelector } from 'store';
import { store } from 'store';
import { setCurrentTokenId } from 'store/actions/globalActions';

import {
  Box,
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Typography,
} from '@mui/material';
import { makeStyles } from '@mui/styles';
import { useWeb3React } from '@web3-react/core';

const BREAKPOINTS = {
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1,
  },
  tablet: {
    breakpoint: { max: 1024, min: 768 },
    items: 1,
  },
  smallTablet: {
    breakpoint: { max: 768, min: 425 },
    items: 1,
  },
  mobile: {
    breakpoint: { max: 425, min: 0 },
    items: 1,
  },
};

const useStyles = makeStyles({
  customOutline: {
    '& .MuiOutlinedInput-notchedOutline': {
      borderColor: '#db9b13',
    },
    '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
      borderColor: '#db9b13',
    },
  },
});

function MintBox({ sx }) {
  const [mintNumber, setMintNumber] = useState(0);
  const [chadinu, setChadinu] = useState(false);

  const { active, account } = useWeb3React();
  const { price, currentTokenId, currentPhase } = useAppSelector(
    (state) => state.global
  );
  console.log('1', currentTokenId, price, account, currentPhase);

  const classes = useStyles();

  useEffect(() => {
    // console.log(data, loading, error);
  }, []);

  const onMint = async () => {
    if (!active) {
      toast.error("Metamask isn't connected.");
      return;
    }
    let tx;
    if (currentPhase === 0 && chadinu) {
      tx = await sugarHeadService.buyNFTWithChadinu(account);
    }
    if (currentPhase === 0 && chadinu === false) {
      tx = await sugarHeadService.buyNFTForFree(account);
    }
    if (currentPhase === 1) {
      tx = await sugarHeadService.buyNFTEarly(account, mintNumber, price);
    }
    if (currentPhase === 2) {
      console.log('mint', account, mintNumber, price);
      tx = await sugarHeadService.buyNFTsInPublic(account, mintNumber, price);
    }
    if (tx !== null) {
      toast.success('Successfully bought!');
      store.dispatch(setCurrentTokenId());
    }
  };

  // useEffect(() => {
  //   console.log(price, currentTokenId);
  // }, [price, currentTokenId]);

  const handleChange = (e) => {
    console.log('chnge', e.target.value);
    setMintNumber(e.target.value);
  };

  const handleRadioChange = (e) => {
    if (e.target.value !== 'female') {
      setChadinu(true);
    } else {
      setChadinu(false);
    }
  };

  return (
    <Box
      sx={{
        backgroundImage: `url(${mintBox})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPosition: 'center top',
        position: 'relative',
        marginBottom: '15px',
        ...sx,
      }}
    >
      <Box
        textAlign="center"
        sx={{
          padding: '15px 10px',
          '@media (min-width: 425px)': {
            padding: '30px 20px',
          },
        }}
      >
        <Typography
          sx={{
            fontFamily: 'LapsusBold',
            fontSize: 25,
            color: 'black',
            '@media (min-width: 425px)': {
              fontSize: 35,
              letterSpacing: '1px',
              WebkitTextStroke: '2px #db9b13',
            },
            '@media (min-width: 768px)': {
              fontSize: 55,
              letterSpacing: '5px',
              WebkitTextStroke: '3px #db9b13',
            },
            '@media (min-width: 1024px)': {
              fontSize: 65,
            },
            '@media (min-width: 1440px)': {
              fontSize: 90,
            },
            WebkitTextStroke: '2px #db9b13',
          }}
        >
          MINT YOUR NFTs NOW
        </Typography>
      </Box>
      <Box
        sx={{
          display: 'grid',
          gridTemplateColumns: 'repeat(1, 1fr)',
          '@media (min-width: 768px)': {
            gridTemplateColumns: 'repeat(2, 1fr)',
          },
        }}
      >
        <Box
          sx={{
            display: 'flex',
            order: 1,
            flexDirection: 'column',
            '@media (min-width: 768px)': {
              padding: '50px 0 50px 0',
              order: 0,
            },
            justifyContent: 'center',
            alignItems: 'center',
          }}
          pb="50px"
        >
          {(currentPhase === 2 || currentPhase === 1) && (
            <Box width="100%" textAlign="center">
              <Typography
                sx={{
                  fontFamily: 'LapsusBold',
                  margin: '20px 0',
                  fontSize: 20,
                  color: 'black',
                  '@media (min-width: 425px)': {
                    fontSize: 26,
                    letterSpacing: '1px',
                    WebkitTextStroke: '2px #db9b13',
                  },
                  '@media (min-width: 768px)': {
                    fontSize: 30,
                  },
                  '@media (min-width: 1024px)': {
                    WebkitTextStroke: '3px #db9b13',
                    fontSize: 33,
                  },
                  '@media (min-width: 1440px)': {
                    fontSize: 50,
                  },
                  WebkitTextStroke: '1px #db9b13',
                }}
              >
                CHOOSE HOW MANY NFTs
              </Typography>
              <FormControl
                sx={{
                  width: '200px',
                  margin: 'auto',
                  '@media (min-width: 768px)': {
                    fontSize: 30,
                  },
                  '@media (min-width: 1024px)': {
                    fontSize: 35,
                  },
                  '& .MuiFormLabel-root': {
                    color: '#db9b13',
                  },
                }}
              >
                <InputLabel
                  id="mint-select-label"
                  sx={{
                    fontFamily: 'LapsusBold',
                    fontSize: '1em',
                    color: '#db9b13',
                  }}
                >
                  Amount
                </InputLabel>
                <Select
                  className={classes.customOutline}
                  labelId="mint-select-label"
                  label="Amount"
                  onChange={handleChange}
                  sx={{
                    fontFamily: 'LapsusBold',
                    fontSize: '1em',
                    textAlign: 'left',
                    color: '#db9b13',
                  }}
                >
                  <MenuItem value={1} sx={{ fontFamily: 'LapsusBold' }}>
                    1
                  </MenuItem>
                  <MenuItem value={2} sx={{ fontFamily: 'LapsusBold' }}>
                    2
                  </MenuItem>
                  <MenuItem value={3} sx={{ fontFamily: 'LapsusBold' }}>
                    3
                  </MenuItem>
                </Select>
              </FormControl>
            </Box>
          )}
          {currentPhase === 0 && (
            <Box>
              <FormControl>
                <FormLabel
                  sx={{
                    fontFamily: 'LapsusBold',
                    fontSize: 30,
                    '@media (min-width: 768px)': {
                      fontSize: 40,
                    },
                    '@media (min-width: 1024px)': {
                      fontSize: 50,
                    },
                    '@media (min-width: 1440px)': {
                      fontSize: 60,
                    },
                    color: 'black',
                  }}
                >
                  Free Mint
                </FormLabel>
                <RadioGroup defaultValue="female" onChange={handleRadioChange}>
                  <FormControlLabel
                    value="male"
                    control={<Radio />}
                    label="Chadinu"
                    sx={{
                      '& .MuiTypography-root': {
                        fontFamily: 'LapsusBold',
                        fontSize: 20,
                        '@media (min-width: 768px)': {
                          fontSize: 30,
                        },
                        '@media (min-width: 1024px)': {
                          fontSize: 40,
                        },
                        '@media (min-width: 1440px)': {
                          fontSize: 50,
                        },
                      },
                    }}
                  />
                  <FormControlLabel
                    value="female"
                    control={<Radio />}
                    label="WhiteList"
                    sx={{
                      '& .MuiTypography-root': {
                        fontFamily: 'LapsusBold',
                        fontSize: 20,
                        '@media (min-width: 768px)': {
                          fontSize: 30,
                        },
                        '@media (min-width: 1024px)': {
                          fontSize: 40,
                        },
                        '@media (min-width: 1440px)': {
                          fontSize: 50,
                        },
                      },
                    }}
                  />
                </RadioGroup>
              </FormControl>
            </Box>
          )}
          <Button
            onClick={onMint}
            sx={{
              fontFamily: 'LapsusBold',
              fontSize: 20,
              padding: '10px 20px',
              '@media (min-width: 425px)': {
                fontSize: 30,
              },
              '@media (min-width: 768px)': {
                fontSize: 40,
              },
              '@media (min-width: 1024px)': {
                fontSize: 50,
              },
              color: 'black',
              backgroundColor: '#db9b13',
              border: '7px solid white',
              borderRadius: '60px',
              maxWidth: '1000px',
              lineHeight: '1',
              letterSpacing: '5px',
              transform: 'rotate(-5deg)',
              marginTop: '150px',
              marginTop: '50px',
              '&:hover': {
                backgroundColor: 'rgba(216,152,16, 0.7)',
              },
            }}
          >
            MINT NOW!
          </Button>
        </Box>
        <Box
          sx={{
            overflow: 'hidden',
            padding: '0 15px',
            textAlign: 'center',
            margin: 'auto',
          }}
        >
          <Box
            component="img"
            src={mintImg}
            sx={{
              boxShadow: '0 0 10px 5px rgba(219, 155, 19, 0.5)',
              width: '250px',
            }}
          />
        </Box>
      </Box>
    </Box>
  );
}

export default MintBox;
